body {
  margin: 0;
  margin-bottom: 50px;
}

#interactive {
  margin: 0 auto;
  position: relative;
  height: 233px;
}

#interactive canvas, #interactive video {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}


